@import '~/styles/variables.scss';

.CraftingItemsTable {
    margin-top: 10px;

    .CraftingItemsTable-title {
        font-size: 1em;
        height: 1.2em;
        text-shadow: $pink 1px 0 1px, $cyan -1px 0 1px;
    }

    .CraftingItemsTable-contents {
        .CraftingItemsTable-row {
            padding: 10px 0;
            border-bottom: 1px solid $light-grey;

            .CraftingItemsTable-row-head {
                display: flex;
                flex-wrap: wrap;
                align-items: center;

                .CraftingItemsTable-row-head-itemName {
                    font-size: 1em;
                    font-weight: bold;
                    height: 1.2em;
                    text-decoration: underline;
                    text-shadow: $pink 1px 0 1px, $cyan -1px 0 1px;
                }

                .CraftingItemsTable-row-head-itemType {
                    margin-left: 10px;

                    .CraftingItemsTable-row-head-itemType-select {
                        font-size: 0.8em;
                        width: 70px;
                        background-color: $black;
                        border: 1px solid $cyan;
                        color: $white;

                        &:focus {
                            outline-color: $pink;
                            outline-style: inset;
                            outline-width: 1px;
                        }
                    }
                }

                .CraftingItemsTable-row-head-plusSelects {
                    display: flex;

                    .CraftingItemsTable-row-head-plusLabel {
                        margin-left: 10px;
                        font-size: 0.8em;
                        line-height: 1.2em;
                        text-shadow: $pink 1px 0 1px, $cyan -1px 0 1px;
                    }

                    .CraftingItemsTable-row-head-plusSelect {
                        margin-left: 10px;
                        font-size: 0.8em;
                        width: 40px;
                        background-color: $black;
                        border: 1px solid $cyan;
                        color: $white;

                        &:focus {
                            outline-color: $pink;
                            outline-style: inset;
                            outline-width: 1px;
                        }
                    }
                }

                .CraftingItemsTable-row-head-delete {
                    margin-left: auto;
                    color: $pink;
                }
            }

            .CraftingItemsTable-row-contents {
                display: flex;

                .CraftingItemsTable-row-contents-controls {
                    display: flex;
                    flex-direction: column;

                    .CraftingItemsTable-row-contents-controls-arrow {
                        color: $cyan;
                        margin-top: 5px;
                        cursor: pointer;

                        &:active {
                            color: $pink;
                        }

                        &.up:hover {
                            text-shadow: $pink 0 -2px;
                        }

                        &.down:hover {
                            text-shadow: $pink 0 2px;
                        }
                    }
                }

                .CraftingItemsTable-row-contents-inputs {
                    margin-left: 10px;
                    display: flex;
                    flex-wrap: wrap;
                }
            }
        }
    }
}